import React from 'react';
import prop from 'prop-types';

const Star = (props) => (
    <svg xmlns="http://www.w3.org/2000/svg" fill={props.fill} className={props.className} viewBox="0 0 24 24">
    
    <path d="M12 .587l3.668 7.568 8.332 1.151-6.064 5.828 1.48 8.279-7.416-3.967-7.417 3.967 1.481-8.279-6.064-5.828 8.332-1.151z"/>

    </svg>
)

export default Star;

Star.defaultProps = {
    fill: '#f7c90e',
    className: 'icon-star'
}

Star.propTypes = {
    fill: prop.string.isRequired,
    className: prop.oneOf(['icon-star', 'star'])
}